import React from 'react'
import { useParams } from 'react-router-dom'

const Blog = ({ props }) => {
  const id = useParams().id
  // console.log('Blog:blogs',props)

  const ShowOrhide = { display: props.visible ? '' : 'none' }

  const blog = props.blogs.find(n => n.id === id)

  return(
    <div style={ShowOrhide}>
      <br />
      <li className='list-group-item'>
        <b id='b-left'>{blog.title}</b>by<b id='b-right'>{blog.author}</b>
      </li>
      <br />
      <li className='list-group-item'>
        <a href={blog.url}>{blog.url}</a>
      </li>
      <li className='list-group-item'>
        {blog.likes} likes
        <button type='button' className='btn btn-success' id='like-button'
          onClick={() => {
            props.likeBlog({ ...blog, user: blog.user.id })
            props.setMessage(`you liked '${blog.content}'`,5)
          }}>like</button>
      </li>
      <li className='list-group-item'>
        added by {blog.user.name}
        {/* ei renderöi kun deletoi */}
        {/* ei renderöi kun deletoi */}
        {/* ei renderöi kun deletoi */}
        {/* ei renderöi kun deletoi */}
        { props.user.name === blog.user.name &&
          <button type='button' id='delete-button' className='btn btn-warning'
            onClick={() => {
              props.history.goBack()
              props.deleteBlog(id)
              props.setMessage(`you deleted '${blog.content}'`,5)
              // props.initializeBlogs()
              // delete props.blogs.id
              props.setBlogs(props.blogs)
              console.log('Blog:deleted',props.blogs)
              props.setVisible(!props.visible)
            }}>delete</button>
        }
        {/* { !visible
          ? <Redirect to='/blogs' />
          : null
        } */}
        <button type='button' id='back-button' className='btn btn-primary'
          onClick={() => {
            props.history.goBack()
            props.setVisible(!props.visible)
          }}
          props={{
            ...props,
          }} >back
        </button>
      </li>
    </div>
  )
}

export default Blog
